.App, body, #root, html {
  height: 100%;
  width: 100%;
}

h1{
  float: left;
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Helvetica Neue", Helvetica, monospace;
  font-size: max(4vh,4vw);
  font-weight: normal;
}

h2{
  margin-left: 1%;
  margin-top: 0;
  padding-top: 25%;
  font-family: "Courier New", Courier, monospace;
  font-style: oblique;
  text-align: center;
}

nav{
  height: max(18vh,18vw);
  width: 100%;
}

#pageNav{
  height: fit-content;
  width: 100%;
}

#pageNavList{
  list-style-type: none;
  margin: 0;
  padding-bottom: 1%;
  height: fit-content;
  width: 100%;
  display: flex;
  flex-direction: column;
}

#noPaddingOrMargin{
  padding-top: 0;
  margin-top: 0;
}

#content{
  height: min(calc(100% - 18vh), calc(100% - 18vw));
  width: 100%;
}

a:link {
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

li {
  float: left;
  padding-left: 2%;
  padding-top: 1%;
}

a {
  display: block;
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-family: "Helvetica Neue", Helvetica, monospace;
  font-size: max(4vh,4vw);
}

p{
  margin-left: 1%;
  margin-top: 0;
  padding-top: 25%;
  font-family: "Courier New", Courier, monospace;
  display: flex;
  justify-content: center;
  text-align: center;
}


#cursor {
  border-left: .1em solid rgb(131, 161, 98);;
  animation: blink .7s steps(1) infinite;
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

#about {
  width: 75%;
  padding-left: 10%;
}
#about-text {
  text-align: left;
}

#back{
  width: 75%;
  padding-left: 0;
}

#projects{
  text-align: center;
}


@media only screen and (min-width: 768px) {
  h1{
    font-size: max(4vh,4vw);
  }
  nav{
    height: max(3vh,3vw);
  }
  ul {
    display: block;
    flex-direction: none;
  }
  #projects{
    display: flex;
    flex-direction: vertical;
    width: 100%;
    text-align: center;
  }
  a,h1{
    font-size: max(2vh,2vw);
  }
  #content{
    height: min(calc(100% - 3vh), calc(100% - 3vw));
  }
}
